import React, { FC } from "react";
import MutItem from "./MutItem";
import s from './Mut.module.scss';

type TData = {
    id: string,
    title: string,
    content: string,
}

const data: TData[] = [
    {
        id: '1',
        title: '  ',
        content: '      7000 .                      7000 .               '
    },
    {
        id: '2',
        title: '  ',
        content: '      7000 .                      7000 .                     7000 .               '
    },
    {
        id: '3',
        title: '  ',
        content: '      7000 .                      7000 .                     7000 .               '
    }, 
    {
        id: '4',
        title: '  ',
        content: '      7000 .                      7000 .               '
    }
];

const Mut: FC = () => {

    return (
        <section className={s.mut}>
            <div className={s.container}>
                {data.map(item => <MutItem title={item.title} content={item.content} key={item.id}/>)}
            </div>
        </section>
    )
}

export default Mut;